import { LayoutType } from '@/typings/layout';
import { NavigationType, PageRouterType } from '@/typings/navigation';
import { createContext } from '@cyberutopian/hooks';
import { Dispatch, SetStateAction } from 'react';

export interface CyberLayoutContextType {
  /** @param  */
  theme: { color: string };
  /** @param  */
  routes: PageRouterType[];
}
export const [CyberLayoutProvider, useCyberLayoutContext, CyberLayoutContext] = createContext<CyberLayoutContextType>();

export interface LayoutContextType {
  /** @param  */
  isExpanded: boolean;
  /** @param  */
  setIsExpanded: Dispatch<SetStateAction<boolean>>;
  /** @param  */
  theme: LayoutType['theme'];
  /** @param  */
  routes: LayoutType['routes'];
}
export const [LayoutProvider, useLayoutContext, LayoutContext] = createContext<LayoutContextType>();

export interface NavigationContextType {
  /** @param  */
  onItemClick?: NavigationType['onItemClick'];
  /** @param  */
  showNav: boolean;
}

export const [NavigationProvider, useNavigationContext, NavigationContext] = createContext<NavigationContextType>();
