import { NavigationMenuType } from '@/typings/navigation';
import { getRouterStack } from '@/utils/bread';
import { useNavigationContext } from '@/utils/context';
import { getNavigationData } from '@/utils/navigate';
import { FC, memo, useEffect, useMemo, useState } from 'react';
import NavigationItem from './navigation-item';

const NavigationList: FC<{ pathname: string; navigationMenu: NavigationMenuType[] }> = ({
  pathname,
  navigationMenu,
}) => {
  const { showNav } = useNavigationContext();

  const { menuList, menuMap, flatMenuList } = useMemo(() => getNavigationData(navigationMenu), [navigationMenu]);

  const defaultOpenValue = useMemo(() => menuList.map((v) => v.value), [menuList]);

  const menuRouteList = useMemo(() => Object.keys(menuMap), [menuMap]);
  // 
  const [activeKey, setActiveKey] = useState<string | undefined>();
  // 
  const [open, setOpen] = useState<string[]>(defaultOpenValue);

  useEffect(() => {
    // 
    if (pathname === '/') {
      setActiveKey(menuMap['/']?.value);
      setOpen(defaultOpenValue);
    } else {
      // eslint-disable-next-line prefer-const
      let { item, menuChildren } = flatMenuList.reduce(
        (pre: { item?: NavigationMenuType; menuChildren: boolean }, next: NavigationMenuType) => {
          if (next.route === pathname) {
            pre.item = next;
          }
          if (next.route && next.route !== '/' && pathname.includes(next.route)) {
            pre.menuChildren = true;
          }
          return pre;
        },
        { item: undefined, menuChildren: false },
      );

      if (!item) {
        if (menuChildren) {
          const stack = getRouterStack().reverse();
          const key = stack.find((v: string) => v !== '/' && menuRouteList.includes(v));

          item = key ? menuMap[key] : undefined;
        } else {
          // 
          item = undefined;
        }
      }

      setActiveKey(item ? item.value : item);
    }

    if (showNav) {
      setOpen(defaultOpenValue);
    } else {
      setOpen([]);
    }
  }, [pathname, showNav, menuRouteList, defaultOpenValue]);

  return (
    <ul className="flex-1 overflow-y-scroll overflow-x-hidden" style={{ paddingRight: showNav ? '8px' : '' }}>
      {menuList.map((v) => {
        const params = {
          data: v,
          activeKey,
          setActiveKey,
          open,
          setOpen,
          showNav: showNav,
          fatherKeys: [],
        };
        return (
          <li key={v.value}>
            <NavigationItem {...params} />
          </li>
        );
      })}
    </ul>
  );
};

export default memo(NavigationList);
