import { BreadcrumbType } from '@/typings/bread';
import { getBreadItems, getBreadMap, getRouterMap, getRouterStack, setRouterMap, setRouterStack } from '@/utils/bread';
import { useLayoutContext } from '@/utils/context';
import { Breadcrumb } from 'antd';
import { ItemType } from 'antd/es/breadcrumb/Breadcrumb';
import { FC, useEffect, useMemo, useState } from 'react';
import { matchRoutes } from 'react-router-dom';

const BreadcrumbNav: FC<BreadcrumbType> = ({ breadConfig }) => {
  const location = window.location;
  const { routes } = useLayoutContext();

  const [breadItems, setBreadItems] = useState<ItemType[]>([]);

  const breadMap = useMemo(() => getBreadMap(breadConfig), [breadConfig]);
  const pathname = useMemo(() => {
    const r = matchRoutes(routes, location);

    return r ? r[r.length - 1]?.route.path : '';
  }, [location.pathname]);

  useEffect(() => {
    const storage = getRouterMap() || {};
    if (pathname) {
      storage[pathname] = {
        pathname: location.pathname,
        params: window.location.search,
      };
      // 
      setRouterMap(storage);
    }

    const stack = getRouterStack();
    if (!stack) return;

    if (stack.slice(-1)[0] !== pathname) {
      stack.push(pathname);
    }
    if (stack.length > 50) {
      stack.shift();
    }

    setRouterStack(stack);

    if (pathname) {
      const items = getBreadItems(stack, breadMap, pathname, routes);
      setBreadItems(items);
    }
  }, [pathname, location?.pathname]);

  return <Breadcrumb items={breadItems} />;
};

export default BreadcrumbNav;
