import { useTransitionend } from '@cyberutopian/hooks';
import { useMemo, useState } from 'react';

interface UseNavVisibleType {
  maxWidth: number;
  minWidth: number;
  isExpanded: boolean;
}

const useNavVisible = ({ maxWidth, minWidth, isExpanded }: UseNavVisibleType) => {
  // 
  const { wrapperDiv: wrapperFatherDiv, transitionend: fatherTransitionend } = useTransitionend();
  // 
  const { wrapperDiv: wrapperChildDiv, transitionend: childTransitionend } = useTransitionend();

  // 
  const [activeNav, setActiveNav] = useState(false);
  // 
  const [forceHide, setForceHide] = useState(false);

  // 
  const navWidth = useMemo(() => {
    if (isExpanded) {
      return maxWidth;
    }
    return activeNav ? maxWidth : minWidth;
  }, [activeNav, isExpanded]);

  // 
  let fatherAnimate = false;
  if (isExpanded) {
    if (fatherTransitionend) {
      fatherAnimate = true;
    }
  }

  let childAnimate = false;
  if (activeNav) {
    if (childTransitionend) {
      childAnimate = true;
    }
  }
  const showNav = forceHide ? false : childAnimate || fatherAnimate;

  return { wrapperFatherDiv, wrapperChildDiv, navWidth, showNav, setActiveNav, setForceHide };
};

export default useNavVisible;
