import { LayoutType } from '@/typings/layout';
import { LayoutProvider } from '@/utils/context';
import { FC, useEffect, useState } from 'react';
import Loading from '../loading';

const Layout: FC<LayoutType> = ({ children, loading, theme, routes, onExpand, ...restProps }) => {
  const [isExpanded, setIsExpanded] = useState(true);

  useEffect(() => {
    onExpand?.(isExpanded);
  }, [isExpanded]);

  if (loading) {
    return <Loading />;
  }

  return (
    <LayoutProvider value={{ isExpanded, setIsExpanded, theme, routes }}>
      <div {...restProps} className={'min-w-[1400px] ' + restProps.className}>
        {children}
      </div>
    </LayoutProvider>
  );
};

export default Layout;
