import { NavigationItemType } from '@/typings/navigation';
import { useLayoutContext, useNavigationContext } from '@/utils/context';
import { FC, MouseEventHandler, cloneElement, memo, useMemo } from 'react';
import SwitchIcon from './switch-icon';

const NavigationItem: FC<NavigationItemType> = (props) => {
  const { data, activeKey, setActiveKey, open, setOpen, fatherKeys } = props;

  const { theme } = useLayoutContext();
  const { showNav, onItemClick } = useNavigationContext();

  const isActive = useMemo(() => data.value === activeKey, [data.value, activeKey]);
  const childLen = data.children?.length || 0;
  const hasChild = useMemo(() => !!childLen, [childLen]);
  const childHeight = useMemo(() => childLen * (44 + 4), [childLen]);

  // 
  const showChild = useMemo(() => open.includes(data.value), [open, data.value]);
  const childFatherKeys = useMemo(() => fatherKeys?.concat(data.value), [fatherKeys, data.value]);

  // 
  const bored = useMemo(() => !!fatherKeys.length, [fatherKeys]);

  const iconComponent = data.icon ? (
    // @ts-ignore
    cloneElement(data.icon, {
      className: 'title-16 ' + (isActive ? 'text-text-1' : 'text-text-5') + ' ',
    })
  ) : (
    <div className={data.icon ? '' : 'pl-[12px]'} />
  );

  const handleClick: MouseEventHandler<HTMLDivElement> = (e) => {
    e.stopPropagation();

    if (hasChild) {
      e.preventDefault();

      const keys = showChild ? open.filter((v) => v !== data.value) : open.concat(data.value);
      setOpen(keys);
    } else {
      setActiveKey(data.value);
    }

    return onItemClick?.(e, data);
  };

  return (
    <>
      <div
        onClick={handleClick}
        className={
          `w-full h-[44px] text-14 box-border px-[14px] py-[11px] mb-[4px] cursor-pointer flex justify-between items-center rounded-[10px] relative z-10 ` +
          (isActive
            ? 'text-[#fff]'
            : bored
              ? 'text-text-5 hover:bg-[#CEDFEF]'
              : 'title-14 text-text-6 hover:bg-[#CEDFEF]')
        }
      >
        <div className="flex justify-between items-center">
          {iconComponent}

          {showNav && (
            <div className="ml-[12px] overflow-hidden" style={{ width: showNav ? 'auto' : 0 }}>
              {data.title}
            </div>
          )}
        </div>
        {hasChild && showNav ? <SwitchIcon className="text-14 text-text-5 p-[4px]" open={showChild} /> : null}
        {isActive && !hasChild ? (
          <div
            className="absolute left-0 w-full h-[44px] rounded-[10px] -z-10"
            style={{ backgroundColor: theme.color }}
          />
        ) : null}
      </div>
      <div
        className="transition-[height] duration-300 ease-in-out overflow-hidden"
        style={{ height: showChild ? `${childHeight}px` : '0px' }}
      >
        {data.children?.map((v: any) => (
          <NavigationItem key={v.value} {...props} data={v} fatherKeys={childFatherKeys} />
        ))}
      </div>
    </>
  );
};

export default memo(NavigationItem);
