import { NavigationMenuType, PageRouterType } from '../typings/navigation';

/**  */
export const getNavigationData = (
  list: NavigationMenuType[],
  fatherKey?: string,
): {
  menuMap: Record<string, NavigationMenuType>;
  menuList: NavigationMenuType[];
  menuOpenKeyMap: Record<string, string[]>;
  flatMenuList: NavigationMenuType[];
} => {
  let menuMap = {};
  let menuList: NavigationMenuType[] = [];
  let flatMenuList: NavigationMenuType[] = [];
  let menuOpenKeyMap = {};

  for (const [index, item] of list.entries()) {
    // if (!item.role.includes(role)) continue;

    let map = {};
    let list: NavigationMenuType[] = [];
    let keyMap = {};

    if (item.children && item.children.length) {
      const data = getNavigationData(item.children, item.value);
      map = data.menuMap;
      list = data.menuList;
      keyMap = data.menuOpenKeyMap;

      flatMenuList = flatMenuList.concat(data.flatMenuList);
    }

    if (item.children && item.children.length) {
      menuMap = { ...menuMap, ...map };
    } else {
      menuMap = { ...menuMap, ...map, [item.route]: { ...item, index } };
    }

    const data = { ...item, children: list };

    flatMenuList = flatMenuList.concat(item);

    menuList = menuList.concat(data);
    menuOpenKeyMap = {
      ...menuOpenKeyMap,
      ...keyMap,
      [item.value]: fatherKey ? [fatherKey, data.value].filter(Boolean) : [],
    };
  }

  return { menuMap, menuList, menuOpenKeyMap, flatMenuList };
};

/**  */
export const formatNavigationData = (role: string, routes: PageRouterType[], parentPath = ''): any[] => {
  return (
    routes
      .filter(
        (item) => item.options?.showNav && (role && item.options?.role ? item.options?.role?.includes(role) : true),
      )
      .map((item) => ({
        title: item.options?.title || '',
        icon: item.options?.icon,
        route: item.path ? parentPath + item.path : undefined,
        value: item.options?.title,
        children: (item.children && formatNavigationData(role, item.children)) || [],
        role: item.options?.role,
      })) || []
  );
};
