import { NavigationType } from '@/typings/navigation';
import { NavigationProvider, useLayoutContext } from '@/utils/context';
import { FC, MouseEventHandler, memo, useEffect } from 'react';
import { matchRoutes } from 'react-router-dom';
import useNavVisible from '../../hooks/useNavVisible';
import NavigationList from './navigation-list';
import ToggleIcon from './toggle-icon';

const Navigation: FC<NavigationType> = ({
  maxWidth,
  minWidth,
  foldUpPath,
  navigationMenu,
  onItemClick,
  titleRender,
  ...restProps
}) => {
  // 
  const { isExpanded, setIsExpanded, routes, theme } = useLayoutContext();

  const { wrapperFatherDiv, wrapperChildDiv, setActiveNav, setForceHide, navWidth, showNav } = useNavVisible({
    maxWidth,
    minWidth,
    isExpanded,
  });

  const pathname = window.location.pathname;

  useEffect(() => {
    const routers = matchRoutes(routes, window.location);
    const path = routers ? routers[routers.length - 1]?.route.path : '';

    if (path && foldUpPath.includes(path as never)) {
      setIsExpanded(false);
      // 
      setForceHide(true);
      setActiveNav(false);
    }
  }, [pathname]);

  const onMouseEnter = () => {
    if (isExpanded) return;
    setActiveNav(true);
    setForceHide(false);
  };

  const onMouseLeave = () => {
    if (isExpanded) return;
    setActiveNav(false);
    setForceHide(false);
  };

  const toggleNavigation: MouseEventHandler<HTMLDivElement> = (e) => {
    e.stopPropagation();

    setIsExpanded((v) => !v);
  };

  return (
    <NavigationProvider value={{ showNav, onItemClick }}>
      <nav
        {...restProps}
        className={'relative h-full transition-[width] duration-300 ease-in-out z-50 ' + restProps.className}
        style={{
          ...restProps.style,
          width: `${isExpanded ? maxWidth : minWidth}px`,
        }}
        ref={wrapperFatherDiv}
        onMouseEnter={onMouseEnter}
        onMouseLeave={onMouseLeave}
      >
        <section
          className="px-[8px] pr-[4px] box-border h-full overflow-x-hidden transition-[width] duration-300 ease-in-out bg-[#EEF5FB] z-70 flex flex-col"
          ref={wrapperChildDiv}
          style={{
            width: navWidth,
            top: '0px',
            left: '0px',
            position: isExpanded ? 'static' : 'absolute',
            backgroundColor: theme.bg,
          }}
        >
          {titleRender?.(showNav)}

          <NavigationList pathname={pathname} navigationMenu={navigationMenu} />

          <div className="text-right pb-[12px] mt-[8px] overflow-hidden" style={{ width: maxWidth - 16 }}>
            <ToggleIcon
              className="text-16 cursor-pointer p-[6px] rounded-[4px] bg-[#F7FBFF]"
              open={isExpanded}
              onClick={toggleNavigation}
            />
          </div>
        </section>
      </nav>
    </NavigationProvider>
  );
};

export default memo(Navigation);
