import { BreadcrumbConfigType } from '@/typings/bread';
import { PageRouterType } from '@/typings/navigation';
import isEqual from 'lodash/isEqual';
import { matchRoutes } from 'react-router-dom';

// key
export const getRouterStoreName = (name: string) => {
  let user = window.localStorage.getItem('user');

  if (user) {
    try {
      user = JSON.parse(user);
    } catch (error) {
      console.log('', error);
    }
  }

  return user ? `${name}` : name;
};

// 
export const setRouterStack = (data: string[], name?: string) => {
  const n = name || getRouterStoreName('routerStack');
  window.localStorage.setItem(n, JSON.stringify(data));
};

// 
export const getRouterStack = (name?: string): any => {
  const n = name || getRouterStoreName('routerStack');
  let data = window.localStorage.getItem(n);

  if (data) {
    try {
      data = JSON.parse(data);
    } catch (error) {
      console.log('', data);
    }
  }

  return data || [];
};

// 
export const setRouterMap = (data: Record<string, { pathname: string; params: string }>, name?: string) => {
  const n = name || getRouterStoreName('routerMap');
  window.localStorage.setItem(n, JSON.stringify(data));
};

// 
export const getRouterMap = (name?: string): any => {
  const n = name || getRouterStoreName('routerMap');
  let data = window.localStorage.getItem(n);

  if (data) {
    try {
      data = JSON.parse(data);
    } catch (error) {
      console.log('', data);
    }
  }

  return data;
};

// 
export const getDetail = (routes: PageRouterType[], pathname: string, path: string) => {
  const paths = matchRoutes(routes, path);
  const m: any = paths ? paths[paths.length - 1]?.route : undefined;

  if (!m) return false;

  const map = getRouterMap();
  const same = pathname === m.path;
  const routerParams = map?.[m?.path as string] || '';
  // TODO  url 
  const href = (same ? undefined : routerParams.params ? path + routerParams?.params : routerParams.pathname)?.replace(
    'undefined',
    '',
  );

  return {
    title: <div className="flex justify-start items-center">{m?.options?.title}</div>,
    href,
    value: m.path,
    className: same ? 'active' : '',
  };
};

const getRouterMatch = (baseArr: string[], routerArr: string[]) => {
  const baseEnd = baseArr.length - 1;
  const routerEnd = routerArr.length;
  // 
  if (baseArr[baseEnd] !== routerArr[0]) {
    return false;
  }

  // 
  let i = baseEnd;
  // 
  let j = 0;
  // 
  const indexArr: number[] = [];

  // TODO: 
  let countSearch = 0;

  while (i > 0 || j < routerEnd) {
    const base = baseArr[i];
    const route = routerArr[j];

    countSearch++;
    //  100 
    if (countSearch >= 100) {
      break;
    }

    if (base === route) {
      indexArr.push(j);

      i--;
      j++;
      if (i < 0 || j > routerEnd) {
        break;
      }
    } else {
      j++;
    }
  }
  return indexArr;
};

const compareIndex = (old: number[], current: number[]) => {
  let bigger = false;
  for (let i = old.length - 1; i >= 0; i--) {
    if (old[i] === current[i]) {
      continue;
    }
    if (old[i] > current[i]) {
      bigger = false;
      break;
    }
    if (old[i] < current[i]) {
      bigger = true;
      break;
    }
  }

  return bigger;
};

// 
export const getBreadItems = (
  stack: string[],
  breadMap: Record<string, Record<string, string[]>>,
  pathname: string,
  routes: PageRouterType[],
) => {
  if (!breadMap[pathname]) {
    return [getDetail(routes, pathname, pathname)];
  }

  const matchStack = [];
  for (const iterator of stack) {
    if (iterator) {
      const items = matchRoutes(routes, iterator);
      const path = items ? items[items.length - 1]?.route.path : undefined;

      path && matchStack.push(path);
    }
  }
  const reverseMatchStack = matchStack.slice().reverse();
  const reverseStack = stack.slice().reverse();

  const arr = Object.values(breadMap[pathname] || {}) || [];

  let items: any[] = [];
  let indexArr: number[] = [];
  let list: string[] = [];

  for (const iterator of arr) {
    const len = iterator.length;
    const data = matchStack.slice(-len);
    if (isEqual(data, iterator)) {
      const data = stack.slice(-len);
      items = data.map((v: string) => getDetail(routes, pathname, v)).filter(Boolean);
      break;
    }
    const iA = getRouterMatch(iterator, reverseMatchStack as string[]) as number[];
    if (Array.isArray(iA) && iA.length === len && !compareIndex(indexArr, iA)) {
      indexArr = iA;
      list = iA.map((v) => reverseStack[v]).reverse();
    }
  }
  if (!items.length) {
    items = list.map((v: string) => getDetail(routes, pathname, v)).filter(Boolean);
  }

  return items;
};

export const getBreadMap = (breadConfig: BreadcrumbConfigType[]) => {
  const map: Record<string, Record<string, string[]>> = {};

  const setMap = (
    list: BreadcrumbConfigType[],
    obj: Record<string, Record<string, string[]>>,
    keys?: string[],
    name?: string, // 
    root?: string, // 
  ) => {
    for (const iterator of list) {
      const data = keys ? keys.concat(iterator.route) : [iterator.route];

      if (iterator.children) {
        let rootName = undefined;
        if (name) {
          // . map
          const nN = obj[iterator.route]?.[name] ? `${root}-${name}` : name;

          obj[iterator.route] = {
            ...(obj[iterator.route] || {}),
            [nN]: data,
            [root as string]: data,
          };
        } else {
          obj[iterator.route] = { [iterator.route]: data };
          rootName = iterator.route;
        }

        setMap(iterator.children, obj, data, iterator.route, root || rootName);
      } else {
        if (root) {
          obj[iterator.route] = { ...(obj[iterator.route] || {}), [root]: data };
        }
        if (name) {
          const nN = obj[iterator.route]?.[name] ? `${root}-${name}` : name;

          obj[iterator.route] = { ...(obj[iterator.route] || {}), [nN]: data };
        }
      }
    }
  };

  setMap(breadConfig, map);

  return map;
};
